const PART = [
    {
        id: 1, category: " ", title: " ,   ?", link: "/", src: "/assets/image/con01.jpg"
    },
    { id: 2, category: "", title: "     .", link: "/", src: "/assets/image/con02.jpg" },
    { id: 3, category: " ", title: ":      .", link: "/", src: "/assets/image/con03.jpg" }
]

const Part2 = () => {
    return (
        <section className="Part2 csc">
            <h2> </h2>
            <div className="inner">
                {
                    PART.map(sec => {
                        return (
                            <figure>
                                <div className="box">
                                    <img src={process.env.PUBLIC_URL + sec.src} alt={sec.title} />
                                </div>
                                <div className="font">
                                    <strong>{sec.category}</strong>
                                    <div className="title">{sec.title}</div>
                                </div>

                            </figure>
                        )
                    })
                }

            </div>
        </section>
    )
}

export default Part2;